<template>
  <view class="page-main-container" :style="{ paddingTop: navBarHeight + 10 + 'px' }">
    <NavBar :navColor="navColor" titleColor="#323232" :title="appName"></NavBar>
    <SwiperList />
    <NoticeBar />
    <KingKongArea />
    <ServiceCard />
    <InstructorRegion />
    <ServiceStyle />
    <Stadium />
    <InstructorPeople />
    <SportsOrg />
    <InstructorSite />
    <paBackToTop :scroll-top="scrollTop" />
  </view>
</template>

<script>
import NavBar from '@/components/navBar/index.vue'
import SwiperList from './components/SwiperList.vue'
import NoticeBar from './components/NoticeBar.vue'
import KingKongArea from './components/KingKongArea.vue'
import ServiceCard from './components/ServiceCard.vue'
import InstructorRegion from './components/InstructorRegion.vue'
import ServiceStyle from './components/ServiceStyle.vue'
import Stadium from './components/Stadium.vue'
import InstructorPeople from './components/InstructorPeople.vue'
import SportsOrg from './components/SportsOrg.vue'
import InstructorSite from './components/InstructorSite.vue'
import { useUserStore } from '@/store/user'
import config from '@/core/config'
import shareMixins from '@/mixins/shareMixins'

export default {
  mixins: [shareMixins],
  components: {
    NavBar,
    SwiperList,
    NoticeBar,
    KingKongArea,
    ServiceCard,
    InstructorRegion,
    ServiceStyle,
    Stadium,
    InstructorPeople,
    SportsOrg,
    InstructorSite,
  },
  data() {
    return {
      navColor: 'transparent',
      showNavBar: false,
      navBarHeight: 0,
      scrollTop: 0,
    }
  },
  computed: {
    appName() {
      return config.appName
    },
    isAuthInstructor() {
      return useUserStore().isAuthInstructor // 是否认证社体指导员
    },
  },
  onLoad(options) {
    let { navBarHeight } = this.$store.app.menuInfo
    this.navBarHeight = navBarHeight
  },
  onPageScroll(event) {
    let scrollTop = event.scrollTop
    if (scrollTop > 60) {
      this.navColor = '#fff'
      this.showNavBar = true
    } else {
      this.navColor = 'transparent'
      this.showNavBar = false
    }
    this.scrollTop = scrollTop
  },
  methods: {
    clickTo(url) {
      uni.navigateTo({
        url: url,
      })
    },
  },
}
</script>

<style lang="scss" scoped>
.page-main-container {
  padding-top: 150rpx;
  padding-bottom: calc(250rpx + env(safe-area-inset-bottom));
  background: url('https://cdn-static.papa.com.cn/social/home-color-bg.png') no-repeat;
  background-color: #fff;
  background-position: 0 0;
  background-size: 100% 530rpx;
}
</style>
